
@import 'normalize.scss';
@import 'reset.scss';

* {
    box-sizing: border-box; }

@font-face {
    font-family: 'MontserratT';
    font-display: swap;
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'MontserratT';
    font-display: swap;
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

html {
    scroll-behavior: smooth;
    font-family: 'MontserratT';
    font-weight: normal;
    line-height: 1.4; }

h1, h2, h3, h4, h5,h6, span {
    line-height: 1; }

$collor-blue: #2275ED;
$collor-orange: #FF662C;
$collor-grey: #ebebeb;
$collor-bg: #F6F6F6;

/* Первая секция */
.container {
    max-width: 1140px;
    margin: 0 auto; }
.main-section {
    position: relative;
    min-height: 800px;
    background: $collor-bg;
    padding-top: 15px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 70px;
        align-items: center; }
    &__content-container {
        position: relative;
        display: flex; }
    &__first-elipse {
        position: absolute;
        top: 155px;
        right: 23px; }
    &__second-elipse {
        position: absolute;
        top: 650px;
        right: -100px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        font-weight: normal;
        &:last-child {
            margin-right: 0; }
        a {
            color: #000;
            text-decoration: none; }
        a:hover {
            text-decoration: underline; } } }

/* Вторая секция */

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 3;
        top: 40px; }
    &__rectangle-blue {
        position: absolute;
        top: 170px;
        left: -80px;
        z-index: 1;
        width: 605.04px;
        height: 220px;
        background: $collor-blue;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
        border-radius: 100px;
        transform: rotate(-60.83deg); }
    &__rectangle-orange {
        position: absolute;
        top: 207px;
        left: -43px;
        z-index: 2;
        width: 605.04px;
        height: 220px;
        background: $collor-orange;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
        border-radius: 100px;
        transform: rotate(-45deg); }
    &__rectangle-grey {
        position: absolute;
        top: 264px;
        left: 3px;
        width: 605.04px;
        height: 220px;
        background: $collor-grey;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
        border-radius: 100px;
        transform: rotate(-23.19deg); } }

.right-wrapper {
    width: 375px;
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        display: flex;
        margin-bottom: 20px; }
    &__title {
        margin-bottom: 20px; }
    &__text {
        font-size: 16px;
        margin-bottom: 40px; } }

.icons {
    &__item {
        margin-right: 18px;
        z-index: 5;
        &:last-child {
            margin-right: 0; } } }

.title {
    font-size: 72px;
    position: relative;
    &__hr {
        position: absolute;
        width: 113px;
        bottom: -10px;
        margin: 0;
        border: none;
        border-top: 1px solid #000;
        &_third {
            position: absolute;
            width: 207px;
            bottom: -10px;
            margin: 0;
            border: none;
            border-top: 1px solid #000; }
        &_footer {
            position: absolute;
            width: 265px;
            bottom: -10px;
            margin: 0;
            border: none;
            border-top: 1px solid #000; } } }

.buttonss {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 205px;
        height: 50px;
        border: none;
        background: $collor-orange;
        border-radius: 100px;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
        z-index: 3;
        font-weight: 600;
        font-size: 16px;
        color: #fff;
        cursor: pointer; }
    &__icon-btn {
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        img {
            margin-right: 12px; } }
    &__text {
        font-size: 16px; } }

.elipse {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 192px;
    height: 192px;
    border-radius: 150px;
    background: linear-gradient(180deg, rgba(255, 102, 44, 0.67) 0%, rgba(0, 102, 255, 0.71) 100%);
    &_big {
        width: 254.22px;
        height: 254.22px; }
    &__subtract {
        width: 130px;
        height: 130px;
        border-radius: 150px;
        background: $collor-bg;
        &_white {
            background: #fff; }
        &_big {
            width: 172px;
            height: 172px;
            background: $collor-bg; } } }

.second-section {
