        &:last-child {
            margin-right: 0px; } }
    &__first-elipse {
        position: absolute;
        top: 410px;
        left: 5%;
        transform: rotate(-138.17deg);
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: 130px;
        left: 82%;
        z-index: -1; } }

.cards-item {
    position: relative;
    width: 360px;
    height: 360px;
    display: flex;
    align-items: flex-end;
    background: $collor-grey;
    border-radius: 20px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 60px;
    &__img {
        height: 335px;
        margin-left: 35px; }
    &__text {
        position: absolute;
        top: 144px;
        right: -45px;
        width: 280px;
        font-size: 32px;
        text-align: center;
        color: #000;
        transform: rotate(90deg); } }

.cards-item_orange {
    background: $collor-orange; }

.cards-item_blue {
    background: $collor-blue; }

.icons-box {
    display: flex;
    justify-content: space-evenly; }

.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        height: 90px;
