        margin-bottom: 40px; }
    &__rectangle-blue {
        position: absolute;
        top: 285px;
        left: 160px;
        width: 402.12px;
        height: 146.22px;
        background: $collor-blue;
        border-radius: 100px;
        transform: rotate(94.61deg);
