    &__rectangle-orange {
        position: absolute;
        top: 259px;
        left: 95px;
        z-index: 2;
        width: 402.12px;
        height: 146.22px;
        background: $collor-orange;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.15);
        border-radius: 100px;
        transform: rotate(126.87deg);
        span {
            position: absolute;
            top: 47px;
            left: 35px;
            font-weight: 600;
            font-size: 64px;
            color: #fff;
            transform: rotate(180deg); } }
    &__rectangle-grey {
