        transform: rotate(163.18deg); } }

.footer {
    position: relative;
    &__img {
        position: absolute;
        right: 7%;
        bottom: 0; }
    &__container {
        display: flex;
        min-height: 275px;
        align-items: center;
        justify-content: {} }
    &__title {
        margin-bottom: 30px; } }

